/*
-------------------------------------------------------------------------------
CSS custom properties (variables)
-------------------------------------------------------------------------------
*/

:root {
    /* White color */

    --white-color: #ffffff;
    
    /* Dark colors */

    --dark-color-light: #25282c; /* Body bg color in dark mode */
    --dark-color-medium: #191c20; /* Main content bg color in dark mode */
    --dark-color-dark: #111417; /* Navbar bg color in dark mode */

    /* Gray colors */
    
    --gray-color-light: #f7f7f7;
    --gray-color-medium: #ecf0f1;
    --gray-color-medium-dark: #dadfe1;
    --gray-color-medium-very-dark: #bababc;
    --gray-color-dark: #47494d;

    /* Primary colors */

    --primary-color: #1890ff;
    --primary-color-light: #45a3fb;
    --primary-color-very-light: #cfe7fd;
    --primary-color-dark: #0d518f;
    --primary-color-very-dark: #172636;
    --primary-box-shadow-color: rgba(24, 144, 255, 0.3);
    --primary-box-shadow-color-darker: rgba(24, 144, 255, 0.6); /* A bit of extra contrast is needed in some places */

    /* Success colors */

    --success-color: #0be881;
    --success-color-light: #81eea8;
    --success-color-very-light: #d5f9e5;
    --success-color-dark: #0d8951;
    --success-color-very-dark: #17312a;
    --success-box-shadow-color: rgba(11, 232, 129, 0.3);
    --success-box-shadow-color-darker: rgba(11, 232, 129, 0.6); /* A bit of extra contrast is needed in some places */

    /* Secondary colors */

    --secondary-color: #ffc312;
    --secondary-color-light: #fbd35b;
    --secondary-color-very-light: #fef1ce;
    --secondary-color-dark: #8c7010;
    --secondary-color-very-dark: #302c1d;
    --secondary-box-shadow-color: rgba(255, 195, 18, 0.3);
    --secondary-box-shadow-color-darker: rgba(255, 195, 18, 0.6); /* A bit of extra contrast is needed in some places */

    /* Danger colors */

    --danger-color: #ff4d4f;
    --danger-color-light: #ed586c;
    --danger-color-very-light: #f8d0d9;
    --danger-color-dark: #8c0e2e;
    --danger-color-very-dark: #301923;
    --danger-box-shadow-color: rgba(245, 34, 45, 0.3);
    --danger-box-shadow-color-darker: rgba(245, 34, 45, 0.6); /* A bit of extra contrast is needed in some places */

    /* Text muted colors */

    --lm-muted-text-color: rgba(0, 0, 0, 0.6);

    --dm-muted-text-color: rgba(255, 255, 255, 0.6);

    /* Navbar */

    --navbar-height: 5rem;

    --lm-navbar-bg-color: var(--white-color);
    --lm-navbar-border-color: rgba(0, 0, 0, 0.2);
    --lm-navbar-box-shadow: none;

    --dm-navbar-bg-color: var(--dark-color-dark);
    --dm-navbar-border-color: rgba(0, 0, 0, 0.2);
    --dm-navbar-box-shadow: var(--lm-navbar-box-shadow);

    --navbar-fixed-bottom-height: var(--navbar-height);

    --lm-navbar-fixed-bottom-box-shadow: none;

    --dm-navbar-fixed-bottom-box-shadow: var(--lm-navbar-fixed-bottom-box-shadow);

    --lm-navbar-static-bottom-box-shadow: var(--lm-navbar-fixed-bottom-box-shadow);

    --dm-navbar-static-bottom-box-shadow: var(--lm-navbar-static-bottom-box-shadow);

    /* Sidebar */

    --sidebar-width: 26rem;

    --lm-sidebar-bg-color: var(--white-color);
    --lm-sidebar-border-color: rgba(0, 0, 0, 0.2);
    --lm-sidebar-box-shadow: none;

    --dm-sidebar-bg-color: var(--dark-color-medium);
    --dm-sidebar-border-color: rgba(0, 0, 0, 0.2);
    --dm-sidebar-box-shadow: var(--lm-sidebar-box-shadow);

    /* Card */

    --lm-card-bg-color: var(--white-color);
    --lm-card-border-color: rgba(0, 0, 0, 0.2);
    --lm-card-box-shadow: none;

    --dm-card-bg-color: var(--dark-color-medium);
    --dm-card-border-color: rgba(0, 0, 0, 0.2);
    --dm-card-box-shadow: var(--lm-card-box-shadow);

    /* Modal content */

    --modal-content-width: 40rem;

    --lm-modal-content-bg-color: var(--lm-card-bg-color);
    --lm-modal-content-border-color: var(--lm-card-border-color);
    --lm-modal-content-box-shadow: var(--lm-card-box-shadow);

    --dm-modal-content-bg-color: var(--dm-card-bg-color);
    --dm-modal-content-border-color: var(--dm-card-border-color);
    --dm-modal-content-box-shadow: var(--dm-card-box-shadow);
}


/*
-------------------------------------------------------------------------------
0.  Normalize CSS

Notes:
    - Normalize.css makes browsers render all elements more consistently and 
    in line with modern standards. It precisely targets only the styles that 
    need normalizing.
    - normalize.css | MIT License | github.com/necolas/normalize.css
    - This is a slighly customized version of normalize.css 8.0.1, 
    specifically for dealing with Halfmoon CSS.
    - Comments have been removed for the sake of compactness.
-------------------------------------------------------------------------------
*/

html {
    line-height: 1.15;
    -webkit-text-size-adjust: 100%;
}
body {
    margin: 0;
}
main {
    display: block;
}
h1 {
    font-size: 2em;
    margin: 0.67em 0;
}
hr {
    box-sizing: content-box;
    height: 0;
    overflow: visible;
}
pre {
    font-family: monospace, monospace;
    font-size: 1em;
}
a {
    background-color: transparent;
}
abbr[title] {
    border-bottom: none;
    text-decoration: underline;
    text-decoration: underline dotted;
}
b,
strong {
    font-weight: bolder;
}
code,
kbd,
samp {
    font-family: monospace, monospace;
    font-size: 1em;
}
small {
    font-size: 80%;
}
sub,
sup {
    font-size: 75%;
    line-height: 0;
    position: relative;
    vertical-align: baseline;
}
sub {
    bottom: -0.25em;
}
sup {
    top: -0.5em;
}
img {
    border-style: none;
}
button,
input,
optgroup,
select,
textarea {
    font-family: inherit;
    font-size: 100%;
    line-height: 1.15;
    margin: 0;
}
button,
input {
    overflow: visible;
}
button,
select {
    text-transform: none;
}
button,
[type="button"],
[type="reset"],
[type="submit"] {
    -webkit-appearance: button;
}
button::-moz-focus-inner,
[type="button"]::-moz-focus-inner,
[type="reset"]::-moz-focus-inner,
[type="submit"]::-moz-focus-inner {
    border-style: none;
    padding: 0;
}
button:-moz-focusring,
[type="button"]:-moz-focusring,
[type="reset"]:-moz-focusring,
[type="submit"]:-moz-focusring {
    outline: 1px dotted ButtonText;
}
fieldset {
    padding: 0.35em 0.75em 0.625em;
}
legend {
    box-sizing: border-box;
    color: inherit;
    display: table;
    max-width: 100%;
    padding: 0;
    white-space: normal;
}
progress {
    vertical-align: baseline;
}
textarea {
    overflow: auto;
}
[type="checkbox"],
[type="radio"] {
    box-sizing: border-box;
    padding: 0;
}
[type="number"]::-webkit-inner-spin-button,
[type="number"]::-webkit-outer-spin-button {
    height: auto;
}
[type="search"] {
    -webkit-appearance: textfield;
    outline-offset: -2px;
}
[type="search"]::-webkit-search-decoration {
    -webkit-appearance: none;
}
::-webkit-file-upload-button {
    -webkit-appearance: button;
    font: inherit;
}
details {
    display: block;
}
summary {
    display: list-item;
}
template {
    display: none;
}
[hidden] {
    display: none;
}
table {
    border-collapse: collapse;
    border-spacing: 0;
}
td,
th {
    padding: 0;
}


/*
-------------------------------------------------------------------------------
1.  Base styles [hm-01]
-------------------------------------------------------------------------------
*/

html {
    -webkit-box-sizing: border-box;
    -moz-box-sizing: border-box;
    box-sizing: border-box;
}
*, *:before, *:after {
    -webkit-box-sizing: inherit;
    -moz-box-sizing: inherit;
    box-sizing: inherit;
}

/* Font related things */

html {
    font-size: 62.5%;
}
@media (min-width: 1600px) {
    html {
        font-size: 75%;
    }
}
@media (min-width: 1920px) {
    html {
        font-size: 87.5%;
    }
}
body {
    background-color: #ffffff;
    color: rgba(0, 0, 0, 0.85);
    font-size: 1.4rem;
    line-height: 1.5;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto, Helvetica, Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji", "Segoe UI Symbol";
    font-weight: 400;
}

/* 
* body.dark-mode is not styled directly so that parts of the page can be 
* dynamically changed to dark mode if needed. However, it should be noted that 
* some things will not work without adding the .dark-mode class to the body. 
*/

.dark-mode {
    background-color: var(--dark-color-light);
    color: rgba(255, 255, 255, 0.8);
    /* 
    * Anti-aliasing works best on light fonts on dark backgrounds. Otherwise, 
    * there is a loss of readability. 
    */
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
}
hr {
    height: 1px;
    color: rgba(0, 0, 0, 0.05);
    background-color: rgba(0, 0, 0, 0.05);
    border: none;
}
.dark-mode hr {
    color: rgba(255, 255, 255, 0.05);
    background-color: rgba(255, 255, 255, 0.05);
}

