-------------------------------------------------------------------------------
*/

.table-responsive {
    overflow-x: auto;
}
.table-responsive .table th,
.table-responsive .table td {
    white-space: nowrap;
}
.table {
    width: 100%;
}
.table thead tr {
    border-bottom: 2px solid var(--gray-color-medium-dark);
}
.dark-mode .table thead tr {
    border-color: var(--gray-color-dark);
}
.table tfoot tr {
    border-top: 2px solid var(--gray-color-medium-dark);
}
.dark-mode .table tfoot tr {
    border-color: var(--gray-color-dark);
}
.table tbody tr:not(:last-child) {
    border-bottom: 1px solid var(--gray-color-medium-dark);
}
.dark-mode .table tbody tr:not(:last-child) {
    border-color: var(--gray-color-dark);
}
